@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,500,700);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
  font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  font-family: 'Quicksand', sans-serif !important;
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
h1 {
	text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 700 !important;
}
#root {
	height: 100vh
}
.content {
	padding: 0 10vw;
}

@media (min-width:961px) {
	.content {
		display: flex;
		justify-content: center;
	}
}

.button-wrapper {
  display: flex;
  justify-content: space-around;
  padding: 32px 0;
  width: 100%;
}

.button-wrapper button {
  height: 100%;
	min-height: 32px;
	box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2);
}
.icon {
  font-size: 40px
}

.home-card {
  padding: 25px;
  border-color: rgba(30, 141, 248, 0.9);
  border-width: 2px;
  border-style: solid;
  border-radius: 8px;
  background-color: white;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-flow: column;
  align-items: center;
}

@media (min-width:961px) {
  .home-card {
    max-width: 20vw;
    min-width: 240px;
    height: 190px;
  }
}

.home-card h1 {
  padding: 10px;
  margin: 0;
}

.home-card button {
  width: 80px;
}
.home {
  display: flex;
  flex-flow: column;
  height: 100vh;
  justify-content: space-evenly;
}

@media (min-width:961px) {
  .home {
    flex-flow: row;
    padding-top: calc(50vh - 85px);
    width: 100%;
  }
}
.app-layout {
  height: 100vh;
  padding: 20vh 0;
  overflow-y: scroll;
  display: flex;
  flex-flow: column;
  align-items: center;
}

@media (min-width:961px) {
  .app-layout {
    max-width: 300px;
  }
}
.radio-group {
  padding: 0 15px;
}

.product-info-button-wrapper {
  display: flex;
  justify-content: space-evenly;
}
.product-panel {
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    margin-right: 5px;
    width: 100%;
}
.item button {
    margin-left: 10px;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2);
}
.lists-table-wrapper {
    width: 100%;
    display: flex;
    flex-flow: column;
    align-items: center;
}
.lists-table {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  margin-right: 5px;
  width: 90%;
  background-color: white;
}
table {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

tbody {
  background-color: white;
}

th {
  font-weight: 700;
}

td {
  text-align: center;
}


